
#toDoList{
  width: 100vw;
  padding-top: 100px;
}

#container{
    width: 60vw;
    margin: auto;
}
@media (max-width: 900px){
#container{
    width: 100vw;
}
}


.task{
    min-height: 6vh;
    margin: auto;
    margin-top: 1vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    border: 1px solid black;
    contain: strict;
    border-radius: 5px;
}
.finished{
    background-color: chartreuse;
}
.finished > h1{
    text-decoration: line-through;
}
.urgent{
    background-color: rgb(206, 0, 0);
}
.task > nav{
    width: 40%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.task >h1{
    padding-left: 5%;
    margin:auto 0;
    font-size: 1rem;
    max-width: 50%;
    contain: content;
}

.taskAdder{
    width: 60vw;
    margin: 0 auto;
}
.taskAdder > input{
    width: 60%;
}
@media (max-width: 900px){
.taskAdder{
    width: 100vw;
    margin: 0;
}
.taskAdder > input{
        width: 80%;
}
}

